/**
 * External dependencies
 */
import { Button, ButtonGroup, Icon } from '@wordpress/components';
import { bug, external } from '@wordpress/icons';

import './style.scss';

const cleanLib = ( lib ) =>
	lib
		.split( /(?=[A-Z])/ )
		.join( '-' )
		.toLowerCase();

const runFunction = ( i ) => {
	const split = i.text.split( '.' );
	const lib = split[ 0 ];
	let func = split[ 1 ];

	console.log( 'Gutenbuild Running: ', i.text );

	if ( 'data' === lib ) {
        if(func.startsWith('select')) {
            wp[ 'data' ][ 'select' ]( cleanLib(i.text) );
          
        } else {
            wp[ 'data' ][ 'select' ]( 'core/editor' );
        }

        return;
	}

	console.log( 'Response: ', wp[lib][func]() );
};

const ClickableList = ( { items, onClick } ) => {
	const formatText = ( type, text ) => {
		switch ( type ) {
			case 'component':
				return `<${ text } />`;
			case 'object':
				return `{${ text }}`;
			default:
				return `${ text }()`;
		}
	};

	return (
		<ul className="gutenbuild-clickable-list">
			{ items.map( ( i ) => {
				const split = i.text.split( '.' );
				const text = split.pop();

				console.log( i );

				return (
					<li key={ i.text }>
						<a href="#" onClick={ () => onClick( i ) }>
							<div className="gutenbuild-clickable-list__title">
								{ formatText( i.type, text ) }
							</div>
							{
								<span
									key={ i }
									className="gutenbuild-clickable-list__subtitle"
								>
									{ split.join( '.' ) }
								</span>
							}
							<div className="gutenbuild-clickable-list__tools">
								<ButtonGroup>
									{ i.type === 'function' && (
										<Button
											isSmall={ true }
											isPrimary={ true }
											onClick={ () => {
												runFunction( i );
											} }
										>
											<Icon size={ 18 } icon={ bug } />
										</Button>
									) }
									<Button
										isSmall={ true }
										isPrimary={ true }
										onClick={ () => {
											window.open( i.link );
										} }
									>
										<Icon size={ 18 } icon={ external } />
									</Button>
								</ButtonGroup>
							</div>
						</a>
					</li>
				);
			} ) }
		</ul>
	);
};

export default ClickableList;
